.features {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
  margin-bottom: 46px;
}

.features__title {
  .fonts(36px; 60px; #111111; none);
  margin: 0;
  margin-bottom: 23px;
  box-sizing: border-box;
  text-align: left;
}

.features__list {
  display: flex;
  flex-direction: column;
  .list(none; 0; 0);
  padding-left: 80px;
}

.features__item {
  .fonts(14px; 18px; #444444; none; @family: @secondFont);
  padding-top: 7px;
  margin-bottom: 19px;

  &--natural {
    position: relative;
  }

  &--food-form {
    position: relative;
  }

  &--food {
    position: relative;
  }

  &--time {
    position: relative;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    position: absolute;
    top: 5px;
    left: -80px;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    background-color: #68b738;
    content: "";

    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50px 50px;
    opacity: 1;
  }
}

.features__item--natural::before {
  background-image: url("/img/advantage-leaf@1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("/img/advantage-leaf@2x.png");
  }
}

.features__item--food-form::before {
  background-image: url("/img/andvantage-powder@1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("/img/advantage-powder@2x.png");
  }
}

.features__item--food::before {
  background-image: url("/img/advantage-dining@1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("/img/advantage-dining@2x.png");
  }
}

.features__item--time::before {
  background-image: url("/img/advantage-clock@1x.png");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("/img/advantage-clock@2x.png");
  }
}

@media (min-width: @tablet-width) {
  .features {
    width: 768px;
    padding: 0 30px;
  }

  .features__title {
    font-size: 60px;
  }

  .features__list {
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
    width: auto;
    margin-bottom: 10px;
    justify-content: space-between;
    padding-left: 0;
  }

  .features__item {
    width: 324px;
    /*min-height: 200px;
    padding-right: 25px;*/
    margin: 0;
    margin-bottom: 82px;
    padding-right: 78px;
    box-sizing: border-box;
    padding-top: 162px;
    letter-spacing: 1.1px;
    line-height: 24px;

    &::before {
      width: 80px;
      height: 80px;
      top: 45px;
      left: 0;
      background-size: 50px 50px;
    }

    &::after {
      position: absolute;
      content: "1";
      display: block;
      top: 9px;
      right: 6px;
      width: 200px;
      height: 290px;
      .fonts(281px; 281px; #f2f2f2; none);
      font-weight: 500;
      z-index: -1;
    }

    &:nth-child(2)::after {
      content: "2";
      right: 13px;
    }

    &:nth-child(3)::after {
      content: "3";
      top: 12px;
      right: 9px;
    }

    &:nth-child(4)::after {
      content: "4";
      right: 10px;
    }
  }

  .features__item--natural {
    &::before {
      top: 48px;
    }
  }

  .features__item--food-form {
    &::before {
      top: 48px;
    }
  }

  .features__item--food {
    &::before {
      top: 50px;
    }
  }

  .features__item--time {
    &::before {
      top: 50px;
    }
  }
}

@media (min-width: @desktop-width) {
  .features {
    width: 1300px;
    padding: 0 40px;
    margin-bottom: 43px;
  }

  .features__title {
    margin-top: 0;
    margin-bottom: 25px;
  }

  .features__item {
    width: 246px;
    padding-top: 159px;
    padding-left: 0;
    padding-right: 0;

    &::before {
      background-size: 50px 50px;
    }

    &::after {
      right: -72px;
      top: 6px;
    }

    &:nth-child(2)::after {
      top: 6px;
      right: -66px;
    }

    &:nth-child(3)::after {
      top: 9px;
      right: -68px;
    }

    &:nth-child(4)::after {
      width: 134px;
      top: 6px;
      right: 0;
    }
  }

  .features__item--food {
    &::before {
      top: 47px;
    }
  }

  .features__item--time {
    &::before {
      top: 47px;
    }
  }
}
